.slide {
  height: 100%;
  width: 100%;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.double{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  div{
    width: 49.5%;
      background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  }
}